<template>
  <v-app>
    <v-container>
      <h1 class="text-center">To-Do List</h1>

      <!-- نموذج إضافة المهام -->
      <v-row>
        <v-col>
          <v-text-field
            v-model="newTask"
            label="Add a new task"
            outlined
          ></v-text-field>
        </v-col>
        <v-col cols="auto">
          <v-btn color="primary" @click="addTask">Add Task</v-btn>
        </v-col>
      </v-row>

      <!-- قائمة المهام -->
      <v-list>
        <v-list-item v-for="(task, index) in tasks" :key="index">
          <v-checkbox
            v-model="task.completed"
            @change="toggleTask(index)"
            color="green"
          ></v-checkbox>
          <v-list-item-content>
            <v-list-item-title :class="{ 'line-through': task.completed }">
              {{ task.name }}
            </v-list-item-title>
          </v-list-item-content>
          <v-list-item-action>
            <v-btn icon color="red" @click="deleteTask(index)">
              <v-icon>mdi-delete</v-icon>
            </v-btn>
          </v-list-item-action>
        </v-list-item>
      </v-list>
    </v-container>
  </v-app>
</template>

<script>
import { useTaskStore } from "./stores/taskstore";

export default {
  data() {
    return {
      newTask: "",
    };
  },
  computed: {
    tasks() {
      return this.taskStore.tasks;
    },
  },
  methods: {
    addTask() {
      if (this.newTask.trim() !== "") {
        this.taskStore.addTask(this.newTask);
        this.newTask = "";
      }
    },
    deleteTask(index) {
      this.taskStore.deleteTask(index);
    },
    toggleTask(index) {
      this.taskStore.toggleTask(index);
    },
  },
  created() {
    this.taskStore = useTaskStore();
  },
};
</script>

<style>
.line-through {
  text-decoration: line-through;
}
</style>
