import { defineStore } from "pinia";

export const useTaskStore = defineStore("taskStore", {
  state: () => ({
    tasks: [], // قائمة المهام
  }),
  actions: {
    addTask(task) {
      this.tasks.push({ name: task, completed: false });
    },
    deleteTask(index) {
      this.tasks.splice(index, 1);
    },
    toggleTask(index) {
      this.tasks[index].completed = !this.tasks[index].completed;
    },
  },
});
